/* Reset */
body {margin:0;padding:0;font-size:0.75em;height:100%;font-family: 'Noto Sans KR', sans-serif;font-weight:400}
html, h1, h2, h3, h4, h5, h6, form, fieldset, img, dl, dt, dd, p {margin:0;padding:0;border:0}
h1, h2, h3, h4, h5, h6 {font-size:1em}
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {display:block}
header ul, nav ul, footer ul {margin:0;padding:0;list-style:none}
legend {position:absolute;font-size:0;line-height:0;text-indent:-9999em;overflow:hidden}
label, input, button, select, img {vertical-align:middle}
input, button {margin:0;padding:0;font-size:1em}
button,input[type=button],input[type=submit]{cursor:pointer;}
input[type=text], input[type=password], input[type=submit], input[type=image], button {border-radius:0;-webkit-appearance:none}
textarea, select {font-size:1em;}
textarea {border-radius:0;-webkit-appearance:none}
p {margin:0;padding:0;word-break:break-all}
pre {overflow-x:scroll;font-size:1.1em}
a:link, a:visited {color:inherit;text-decoration:none}
ul,ol,li{list-style:none;padding:0;margin:0;}
input, button, textarea, select {font-family: 'Noto Sans KR', sans-serif; outline: none}
hr {content: '';display:block;padding:0;border:0;margin: 0;}

/*사파리 버튼 초기화*/
input[type="text"], input[type="password"], input[type="button"], input[type="submit"], textarea { -webkit-appearance: none; -webkit-border-radius: 0; }


select {
    -webkit-appearance: none; /* 화살표 없애기 for chrome*/
    -moz-appearance: none; /* 화살표 없애기 for firefox*/
    appearance: none; /* 화살표 없애기 공통*/
    /* background-image: url('../img/ico_arrow_select1.png'); */
    background-repeat: no-repeat;
    background-position: calc(100% - 16px) center;
    background-size: 24px;
    background-color: #fff;
    cursor: pointer; 
}
select::-ms-expand { display: none; /* 화살표 없애기 for IE10, 11*/ }


/*--------------------------------------------------------------------------------------------------------------------------*/


/*공통 CSS*/
/*layout style*/
section > .wrap { padding: 0;}

.content { position: relative; width: 1650px; max-width: 100%; margin: 0 auto; }

/*position style*/
.pos { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%) scale(1); -webkit-transform: translate(-50%, -50%) scale(1); -moz-transform: translate(-50%, -50%) scale(1); -ms-transform: translate(-50%, -50%) scale(1); -o-transform: translate(-50%, -50%) scale(1); width: 100%; }


/*게시판 검색*/
.search-tool {top: -88px; left: 0; right: 0; max-width: 100%; height: 100%; padding: 20px; border-radius: 5%; box-shadow: 0 4px 16px 0 rgba(0, 0, 0, 0.15); box-sizing: border-box; background: #fff; }
.search-tool .filter { width: 100%;}
.search-tool .filter li { display: flex; flex-direction: column; justify-content: center; padding: 15px 15px 15px;}
.search-tool .filter li:first-child { box-sizing: border-box; }
.search-tool .filter .sort { margin-bottom: 4px; font-size: 12px; font-weight: 400; color: #918665; }

.search-tool .filter-in li { display: flex; flex-direction: column; justify-content: center; padding: 15px 10px 15px; border-bottom: 1px solid #dde1e2;}
.search-tool .filter-in li:first-child { box-sizing: border-box; }
.search-tool .filter-in .sort { margin-bottom: 4px; font-size: 12px; font-weight: 400; color: #918665; }

.search-tool .filter select { border: none; background-size: 24px; font-size: 16px; font-weight: 400; color: #000; }
